import React from 'react';

//    
import MyChildrenSub from './MyChildrenSub';

const MyChildren = () => {
    return (
        <div>
            <h2>MyChildren</h2>
            <MyChildrenSub color="red" size={7}> React</MyChildrenSub>
            <MyChildrenSub color="green" size={5}><b>Hello World</b></MyChildrenSub>
            <MyChildrenSub color="blue" />
        </div>
    );
};

export default MyChildren;